import { Component, ViewChild, OnInit } from '@angular/core';
import { Nav, Platform, MenuController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { IonicProvider, AuthProvider } from '../providers';
import { Storage } from '@ionic/storage';
import firebase from 'firebase/app';
import 'firebase/auth';
import { HomePage, LoginPage, OrdersPage, StoresPage, ScanPage } from '../pages';
// import { ComplainsPage } from '../pages/complains/complains';

@Component({
  templateUrl: 'app.html',
})
export class MyApp implements OnInit {
  @ViewChild(Nav) nav: Nav;
  rootPage: any;
  user;
  token_sub;
  pages: Array<{title: string, component: any}>;
  constructor(public platform: Platform, private auth$: AuthProvider, public storage: Storage, public statusBar: StatusBar, private ionic$: IonicProvider, public splashScreen: SplashScreen, public menuCtrl: MenuController) {
    this.auth$.start_listening();
    firebase.auth().setPersistence(firebase.auth.Auth.Persistence.LOCAL);
    this.platform.ready().then(() => {
      this.storage.ready().then(() => {
        this.storage.get("currentUser").then(user => {
          if(user && firebase.auth().currentUser){
            this.rootPage = HomePage
          }else{
            this.rootPage = LoginPage
          }
        })
      })
      this.statusBar.styleLightContent();
      this.statusBar.overlaysWebView(false)
      this.statusBar.backgroundColorByHexString("#0774C6");
      this.splashScreen.hide();
    });
    this.pages = [
      { title: ' ', component: HomePage },
      { title: ' ', component: OrdersPage },
      { title: ' ', component: StoresPage },
      { title: '  ', component: ScanPage },
    ];
  }
  openPage(page) {
    if(this.nav.getActive().component != page.component) this.nav.setRoot(page.component);
  }
  
  ngOnInit(){
    this.auth$.user.subscribe(user => {
      if(user){
        this.user = user;
      }else{
        this.nav.setRoot(LoginPage);
      }
    })
  }
}



