import { Component, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OrderProvider, IonicProvider, AuthProvider } from '../../providers';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { CallNumber } from '@ionic-native/call-number';
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import { Storage } from '@ionic/storage';
// import { QRScanner, QRScannerStatus } from '@ionic-native/qr-scanner';

@IonicPage()
@Component({
  selector: 'page-scan',
  templateUrl: 'scan.html',
})
export class ScanPage implements OnDestroy {
  isLoading = false;
  orders = [];
  status = "";
  notes = "";
  scheduled_date;
  user;
  current_barcode = "";
  constructor(public navCtrl: NavController, private auth$: AuthProvider, private storage$: Storage, private ionic$: IonicProvider, private call$: CallNumber, public navParams: NavParams, private order$: OrderProvider, private barcode$: BarcodeScanner) {
    this.auth$.getUser().then(user => {
      this.user = user;
    })
  }
  ngOnInit(){
    // this.storage$.ready().then(() => {
    //   this.storage$.get("scanned_orders").then(orders => {
    //     if(orders){
    //       orders.map((order, index) => {
    //         this.order$.order(order.id).get().then((doc) => {
    //           if(doc.exists) {
    //             this.orders.push({ id: doc.id, ...doc.data(), store: order.store })
    //           }
    //           if(index == orders.length-1) this.update_orders();
    //         })
    //       })
    //     }
    //   });
    // });
  }
  clearAll(){
    this.orders = [];
    this.update_orders();
  }
  scanBarCode(){
    this.barcode$.scan().then(barcodeData => {
      this.search_order(barcodeData.text)
    }).catch(err => {
      this.ionic$.presentToast("Something Went Wrong", "long", "bottom");
      console.log('Error', err);
    });
  }
  scanDashboard(){
    this.barcode$.scan().then(barcodeData => {
      this.current_barcode = barcodeData.text;
      this.ionic$.presentToast("      !", "short", "bottom");
    }).catch(err => {
      this.ionic$.presentToast("Something Went Wrong", "long", "bottom");
      console.log('Error', err);
    });
  }
  // scanQrCode(){
  //   this.qrScanner.prepare()
  //   .then((status: QRScannerStatus) => {
  //     if (status.authorized) {
  //       let scanSub = this.qrScanner.scan().subscribe((text: string) => {
  //         this.search_order(text, "qrcode")
  //         this.qrScanner.hide();
  //         scanSub.unsubscribe();
  //       });
  //     } else if (status.denied) {
  //       this.ionic$.presentToast("Please give permissions", "long", "bottom");
  //     } else {
  //       this.ionic$.presentToast("Please give permissions", "long", "bottom");
  //     }
  //   })
  //   .catch((e: any) => console.log('Error is', e));
  // }

  addScannedOrder() {
    this.isLoading = true;
    let updated_orders = 0;
    this.orders.map((order, idx) => {
      this.order$.updateScannedOrder(order, {
        scannedOrder:{
          user:this.user,
          scanned_at:new Date()
        },
      }).then(() => {
        updated_orders++;
        if(updated_orders == this.orders.length){
          this.isLoading = false;
          this.ionic$.presentToast("finished updating", "short", "bottom");
        }
      }, err => {
        updated_orders++;
        this.ionic$.presentToast(order.client_name+" is not updated", "short", "bottom");
        if(updated_orders == this.orders.length){
          this.isLoading = false;
          this.ionic$.presentToast("finished updating", "short", "bottom");
        }
      })
    })
  }
  applyAction(){
    if(!this.status && !this.notes) {
      this.ionic$.presentToast("Please choose a status", "long", "bottom");
      return;
    }
    if(this.status == "scheduled" && !this.scheduled_date){
      this.ionic$.presentToast("   ", "long", "bottom");
      return;
    }
    this.isLoading = true;
    let updated_orders = 0;
    this.orders.map((order, idx) => {
      this.order$.update(order, {
        status: this.status,
        scannedOrder:{
          user:this.user,
          scanned_at:new Date()
        },
        scheduled_date: (this.scheduled_date)?new Date(this.scheduled_date):null,
        notes: (this.notes)?this.notes:""
      }).then(() => {
        updated_orders++;
        if(updated_orders == this.orders.length){
          this.isLoading = false;
          this.ionic$.presentToast("finished updating", "short", "bottom");
        }
      }, err => {
        updated_orders++;
        this.ionic$.presentToast(order.client_name+" is not updated", "short", "bottom");
        if(updated_orders == this.orders.length){
          this.isLoading = false;
          this.ionic$.presentToast("finished updating", "short", "bottom");
        }
      })
    })
  }
  search_order(code, type="barcode"){
    let remove_orders = true;
    let orders_ref:any = firebase.firestore().collection("orders");
    code = code.trim();
    // code = code.toLowerCase();
    if(code == "c1") {
      orders_ref = orders_ref.where("status", "==", "cancelled")
    }else if(code == "s1") {
      let day_start = new Date();
      day_start.setDate(day_start.getDate()+1)
      day_start.setHours(0, 0, 0, 0);
      let day_end = new Date();
      day_end.setDate(day_end.getDate()+1)
      day_end.setHours(24, 60, 60, 1000);
      orders_ref = orders_ref.where("status", "==", "scheduled").where('scheduled_date', '>=', day_start.getTime()).where('scheduled_date', '<=', day_end.getTime()).orderBy('scheduled_date', 'desc')
    }else if(code == "s2") {
      let day_start = new Date();
      day_start.setDate(day_start.getDate()+2)
      day_start.setHours(0, 0, 0, 0);
      let day_end = new Date();
      day_end.setDate(day_end.getDate()+2)
      day_end.setHours(24, 60, 60, 1000);
      orders_ref = orders_ref.where("status", "==", "scheduled").where('scheduled_date', '>=', day_start.getTime()).where('scheduled_date', '<=', day_end.getTime()).orderBy('scheduled_date', 'desc')
    }else if(code == "s3") {
      let day_start = new Date();
      day_start.setDate(day_start.getDate()+3)
      day_start.setHours(0, 0, 0, 0);
      let day_end = new Date();
      day_end.setDate(day_end.getDate()+3)
      day_end.setHours(24, 60, 60, 1000);
      orders_ref = orders_ref.where("status", "==", "scheduled").where('scheduled_date', '>=', day_start.getTime()).where('scheduled_date', '<=', day_end.getTime()).orderBy('scheduled_date', 'desc')
    }else if(code == "s4"){
      let day_start = new Date();
      day_start.setDate(day_start.getDate()+4)
      day_start.setHours(0, 0, 0, 0);
      let day_end = new Date();
      day_end.setDate(day_end.getDate()+4)
      day_end.setHours(24, 60, 60, 1000);
      orders_ref = orders_ref.where("status", "==", "scheduled").where('scheduled_date', '>=', day_start.getTime()).where('scheduled_date', '<=', day_end.getTime()).orderBy('scheduled_date', 'desc')
    }else if(code == "s5"){
      let day_start = new Date();
      day_start.setDate(day_start.getDate()+4)
      day_start.setHours(0, 0, 0, 0);
      orders_ref = orders_ref.where("status", "==", "scheduled").where('scheduled_date', '>=', day_start.getTime()).orderBy('scheduled_date', 'desc')
    }else{
      remove_orders = false;
      orders_ref = orders_ref.where("barcode", "==", code)
    }
    // }else{
    //   orders_ref = orders_ref.where("store", "==", firebase.firestore().collection("stores").doc(code)).where("status", "==", "cancelled")
    // }
    orders_ref.get().then(snapshot => {
      if (!snapshot.empty){
        if(remove_orders){
          this.orders = [];
          this.update_orders();
        }
        snapshot.docs.map((doc, index) => {
          let order:any = { id: doc.id, ...doc.data() }
          
          if(this.current_barcode){
            doc.ref.update({
              scan_code: this.current_barcode
            })
          }
          let idx = this.orders.findIndex((or) => or.id == order.id);
          if(idx == -1){
            order.store.get().then(store => {
              order.store = { id: store.id, ...store.data() }
              this.orders.push(order);this.update_orders();
              if(index == snapshot.docs.length) this.isLoading = false;
            }, err => {
              this.ionic$.presentToast("    !")
              if(index == snapshot.docs.length) this.isLoading = false;
            })
          }else{
            order.store = this.orders[idx].store;
            this.orders[idx] = order;this.update_orders();
            if(index == snapshot.docs.length) this.isLoading = false;
          }
        })
      }else{
        this.ionic$.presentToast("    !");
        this.isLoading = false;
      }
    }, err => {
      this.isLoading = false;
      this.ionic$.presentToast("    !")
    })
  }
  update_orders(){
    // this.storage$.ready().then(() => {
    //   let orders;
    //   if(this.orders && this.orders.length){
    //     orders = this.orders.map((ord) => {
    //       return { id: ord.id, store: ord.store }
    //     })
    //   }else{
    //     orders = [];
    //   }
    //   this.storage$.set("scanned_orders", orders);
    // });
  }
  remove_order(id){
    let idx = this.orders.findIndex((or) => or.id == id);
    this.orders.splice(idx, 1);this.update_orders();
  }
  call(phone_number){
    this.call$.callNumber(phone_number, false)
  }
  ngOnDestroy(){
  }
}



