import * as bcrypt from 'bcryptjs';
export let hash = (string) => {
    return new Promise((resolve, reject) => {
        bcrypt.hash(string, "$2a$12$NbPH5lb6uWMaZhPWLPzFLu").then((hash) => {
            resolve(hash)
        }, err => reject(err))
    })
}


