import { Component, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OrderProvider, IonicProvider, AuthProvider } from '../../providers';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { CallNumber } from '@ionic-native/call-number';
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-orders',
  templateUrl: 'orders.html',
})
export class OrdersPage implements OnDestroy {
  isLoading = true;
  orders = [];
  area = "";
  zones = [];areas_obj:any = {};
  constructor(public navCtrl: NavController, private auth$: AuthProvider, private storage$: Storage, private ionic$: IonicProvider, private call$: CallNumber, public navParams: NavParams, private order$: OrderProvider, private barcode$: BarcodeScanner) {
    this.order$.get_settings().then(settings => {
      for (const key in settings.zones) {
        this.zones.push({
          key,
          value: settings.zones[key]
        })
        this.areas_obj[key] = settings.zones[key];
      }
    })
  }
  ngOnInit(){}
  scanBarCode(){
    this.barcode$.scan().then(barcodeData => {
      if(barcodeData.text) this.search(barcodeData.text)
    }).catch(err => {
      this.ionic$.presentToast("Something Went Wrong", "long", "bottom");
      console.log('Error', err);
    });
  }
  search(areaCode=null){
    let orders_ref:any = firebase.firestore().collection("orders").limit(100);
    let several_codes = false;
    if(areaCode){
      let codes = areaCode.split(' ');
      if(codes.length > 1){
        several_codes = true
        this.isLoading = true;
        this.orders = [];
        codes.map((code) => {
          if(this.areas_obj[code]){
            let code_orders_ref:any = firebase.firestore().collection("orders").where('client_area', '==', code).limit(100);
            this.db_search(code_orders_ref.where("status", "==", "sorted"), false);
            this.db_search(code_orders_ref.where("status", "==", "scheduled"), false);
            this.db_search(code_orders_ref.where("status", "==", "unresponsive"), false);
          }
        })
      }else{
        if(areaCode && this.areas_obj[areaCode]){
          orders_ref = orders_ref.where("client_area", "==", areaCode);
        }else if(this.area){
          orders_ref = orders_ref.where("client_area", "==", this.area)
        }
      }
    }else if(this.area){
      orders_ref = orders_ref.where("client_area", "==", this.area)
    }
    if(!several_codes) {
      this.isLoading = true;
      this.db_search(orders_ref.where("status", "==", "sorted"));
      this.db_search(orders_ref.where("status", "==", "scheduled"), false);
      this.db_search(orders_ref.where("status", "==", "unresponsive"), false);
    }
  }
  db_search(orders_ref, remove_orders=true){
    orders_ref.get().then(snapshot => {
      if (!snapshot.empty){
        if(remove_orders) this.orders = [];
        snapshot.docs.map((doc, index) => {
          let order:any = { id: doc.id, ...doc.data() }
          let idx = this.orders.findIndex((or) => or.id == order.id);
          if(idx == -1){
            order.store.get().then(store => {
              order.store = { id: store.id, ...store.data() }
              this.orders.push(order);
              this.isLoading = false;
            }, err => {
              this.ionic$.presentToast("    !")
              this.isLoading = false;
            })
          }else{
            order.store = this.orders[idx].store;
            this.orders[idx] = order;
            this.isLoading = false;
          }
        })
      }else{
        // this.ionic$.presentToast("    !");
        this.isLoading = false;
      }
    }, err => {
      this.isLoading = false;
      this.ionic$.presentToast("    !")
    })
  }
  ngOnDestroy(){
  }
}



