import { Component, OnInit, OnDestroy, NgZone } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController } from 'ionic-angular';
import { ChatProvider, IonicProvider, StorageProvider, AuthProvider } from '../../providers';
import { Camera } from '@ionic-native/camera';

@IonicPage()
@Component({
  selector: 'page-messaging',
  templateUrl: 'messaging.html',
})
export class MessagingPage implements OnDestroy {
  isLoading = false;
  no_data = false;
  end = null;
  all_orders = [];
  no_data_options = {
    text: "   "
  }
  message = {
    image: "",
    text: ""
  };
  // messages list
  messages = [];
  // messages listener
  messages_listener;

  // store
  store;
  constructor(
    public navCtrl: NavController, private actionSheetCtrl: ActionSheetController, private chat$: ChatProvider, private auth$: AuthProvider, private ngZone: NgZone,
    private camera: Camera, public navParams: NavParams, private ionic$: IonicProvider, private storage$: StorageProvider) {
    this.isLoading = true;
    this.store = this.navParams.get("store");
    this.messages_listener = this.chat$.listen_messages(this.store.id).limit(20).onSnapshot((snapshot) => {
      this.ngZone.run(() => {
        if(!snapshot.empty){
          snapshot.docChanges.map((change, index) => {
            let message:any = { id: change.doc.id, ...change.doc.data() };
            message.created_at = new Date(message.created_at);
            let messageIdx = this.messages.findIndex((ms) => ms.id == message.id);
            if(change.type == "added"){
              if(messageIdx === -1){
                this.messages.push(message);
              }else{
                this.messages[messageIdx] = message;
              }
            }else if(change.type == "removed"){
              this.messages.splice(messageIdx, 1);
            }
          })
          this.isLoading = false;
        }else{
          this.isLoading = false;
        }
      })
    })
  }
  ngOnDestroy(){
    if(this.messages_listener) this.messages_listener();
  }
  send(){
    if(!this.message.text){
      this.ionic$.presentToast("   ");
      return;
    }
    this.isLoading = true;
    this.chat$.send_message({
      text: this.message.text
    }, this.store.id).then((doc) => {
      this.isLoading = false;
      this.message.text = "";
    }, err => {
      this.isLoading = false;
      console.log(err);
      this.ionic$.presentToast("        ");
    })
  }
  presentActionSheet() {
		let actionSheet = this.actionSheetCtrl.create({
    title: ' ',
      buttons: [
      {
        text: '  ',
        handler: () => {
          this.takePicture(this.camera.PictureSourceType.PHOTOLIBRARY);
        }
      },
      {
        text: ' ',
        handler: () => {
          this.takePicture(this.camera.PictureSourceType.CAMERA);
        }
      },
      {
        text: '',
        role: 'cancel'
      }
      ]
		});
		actionSheet.present();
		return false;
  }
  
  takePicture(sourceType) {
    this.camera.getPicture({
      quality: 30,
      sourceType: sourceType,
      destinationType: this.camera.DestinationType.DATA_URL
    }).then((imageData) => {
      if(imageData){
        this.isLoading = true;
        this.storage$.put(this.storage$.b64toBlob(imageData, "image/jpg")).then((downloadURL) => {
          this.chat$.send_message({
            image: downloadURL
          }, this.store.id).then((doc) => {
            this.isLoading = false;
          }, err => {
            this.isLoading = false;
            console.log(err);
            this.ionic$.presentToast("        ");
          })
        }, err => {
          this.ionic$.presentToast("        ");
          this.isLoading = false;
        })
      }
    }, (err) => {
      console.log(JSON.stringify(err));
    });
  }
}



