import { Injectable } from "@angular/core";
import "rxjs/add/operator/map";
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import { md5 } from "./md5.provider";
import { AuthProvider } from "./auth.provider";

@Injectable()
export class OrderProvider {
    constructor(private auth$: AuthProvider){}
    order(id){
        return firebase.firestore().collection("orders").doc(id)
    }
    updateScannedOrder(order, data) {
        return new Promise((resolve, reject) => {
            let update_data:any = {};
           

            firebase.firestore().collection("orders").doc(order.id).get().then(order_doc => {
                if(order_doc.exists){
                    let odata = order_doc.data()
                    order_doc.ref.update({
                        scannedOrder:{
                            user: this.auth$._user,
                            scanned_at:new Date()
                          },
                       
                    })
                }
            });
            update_data.client_confirm_notes = data.notes;
            if(data.status == "scheduled" || data.status == "unresponsive"){
                update_data.client_confirmed = data.status;
                update_data.client_confirm_date = new Date()
            }
            
            if(data.status == "scheduled"){
                update_data.scheduled_date = data.scheduled_date.getTime();
            }
            update_data.updated_at = new Date().getTime();
            firebase.firestore().collection("orders").doc(order.id).update(update_data).then(() => {
                resolve(order)
            }, reason => {
                console.log("order error");
                reject(reason)
            })
        })
    }
    update(order, data) {
        return new Promise((resolve, reject) => {
            let update_data:any = {};
            if(data.status){
                if(order.status != data.status){
                    // if(data.status == "returned"){
                    //     update_data.returned = true;
                    //     if(order.status != "cancelled"){
                    //         update_data.status = "cancelled"
                    //     }
                    // }else{
                        update_data.status = data.status
                    // }
                    
                    if(status == "received") update_data.confirmed = true;
                    if(data.status == "delivered"){
                        if(!data.delivery_date){
                            update_data.delivery_date = new Date().getTime();
                        }
                    }
                    if(!order.driver && (data.status == "delivered" || data.status == "cancelled")) update_data.driver = firebase.firestore().collection("drivers").doc("yekivcDFhEQyWRlVmu17zOoJAFt2")
                    if(!order.collection){
                        update_data.collection = md5(order.id+Math.random().toString(36).substr(2, 9));
                        update_data.collection_timestamp = new Date().getTime();
                    }
                    if(data.status == "delivered"){
                        update_data.collect_amount = order.price+((order.delivery_fees)?order.delivery_fees:update_data.delivery_fees);
                        update_data.collect_notes = data.collect_notes;
                    }
                    if(data.status == "created" && order.status != "created"){
                        update_data.driver = null
                    }
                }
                if(data.status == "delivered" || order.status == "delivered"){
                    if(data.delivery_date){
                        update_data.delivery_date = data.delivery_date;
                    }
                }
            }

            firebase.firestore().collection("orders").doc(order.id).get().then(order_doc => {
                if(order_doc.exists){
                    let odata = order_doc.data()
                    order_doc.ref.update({
                        scannedOrder:{
                            user: this.auth$._user,
                            scanned_at:new Date()
                          },
                        status_history: [...(odata.status_history?odata.status_history:[]), {
                            status: data.status,
                            notes: (data.notes)?data.notes:"",
                            user: {
                                ...this.auth$._user,
                                password: null
                            },
                            type: "scanner",
                            created_at: new Date()
                        }]
                    })
                }
            });
            update_data.client_confirm_notes = data.notes;
            if(data.status == "scheduled" || data.status == "unresponsive"){
                update_data.client_confirmed = data.status;
                update_data.client_confirm_date = new Date()
            }
            
            if(data.status == "scheduled"){
                update_data.scheduled_date = data.scheduled_date.getTime();
            }
            update_data.updated_at = new Date().getTime();
            firebase.firestore().collection("orders").doc(order.id).update(update_data).then(() => {
                resolve(order)
            }, reason => {
                console.log("order error");
                reject(reason)
            })
        })
    }
    confirm_order(code){
        return new Promise((resolve, reject) => {
            code = code.toLowerCase()
            firebase.firestore().collection("orders").where("barcode", "==", code).get().then(snapshot => {
                if (!snapshot.empty){
                    let dt:any = { confirmed: true, searching: false, confirm_date: new Date() };
                    if(snapshot.docs[0].data().status == 1) dt.status = 2
                    snapshot.docs[0].ref.update(dt).then(() => {
                        snapshot.docs[0].ref.collection("status").add({
                            source: 'flick',
                            status: 8,
                            created_at: new Date()
                        })
                        resolve()
                    }, err => reject(err))
                }else{
                    reject()
                }
            })
        })
    }
    get_settings() :any {
        return new Promise((resolve, reject) => {
            firebase.firestore().collection("system").get().then(snapshot => {
                if(!snapshot.empty){
                    let data:any = {}
                    snapshot.docs.map((doc) => {
                        data = { ...data, ...doc.data() }
                    })
                    resolve(data)
                }else{
                    resolve()
                }
            })
        })
    }
    update_pickup(id, status){
        return new Promise((resolve, reject) => {
            firebase.firestore().collection("pickups").doc(id).update({ updated_at: new Date(), status }).then(() => resolve(), err => reject(err));
        })
    }
    get_pickups(){
        let day_start = new Date();
        day_start.setHours(0, 0, 0, 0);
        let day_end = new Date();
        day_end.setHours(24, 60, 60, 1000);
        return firebase.firestore().collection('pickups').where('status', '==', 0).where('pickup_date_timestamp', '>=', day_start.getTime()).where('pickup_date_timestamp', '<=', day_end.getTime()).orderBy('pickup_date_timestamp', 'desc')
    }
}


