import { Injectable, ViewChild } from '@angular/core';
// import * as firebase from 'firebase';
import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import { BehaviorSubject } from 'rxjs';
import { Storage } from '@ionic/storage';
import { UserModel } from '../models/user.model';
import { IonicProvider } from './ionic.provider';
import { md5 } from './md5.provider';
import { hash } from './bcrypt.provider';

@Injectable()
export class AuthProvider {
    user: BehaviorSubject<UserModel> = new BehaviorSubject(null);
    _user;
    constructor(
        private storage: Storage,
        private ionic$: IonicProvider
    ) {}
    start_listening(){
        this.user.subscribe(user => {
            this._user = user
        })
        this.storage.ready().then(() => {
            this.storage.get("currentUser").then(user => {
                if(user){
                    let u = JSON.parse(user);
                    this.user.next(u)
                }
            })
        })
    }
    logIn({ email, password }: { email: string; password: string }) {
        return new Promise((resolve, reject) => {
            hash(password).then((password_hash) => {
                firebase.auth().signInWithEmailAndPassword(email, password).then((res) => {
                    firebase.firestore().collection("admins").doc(res.uid).get().then(snapshot => {
                        if(snapshot.exists){
                            let user: any = { id: snapshot.id, ...snapshot.data() } as UserModel;
                                this.storage.clear();
                                this.storage.set('currentUser', JSON.stringify(user));
                                this.user.next(user)
                                resolve(user)
                            
                        }else{
                            reject("   !")
                        }
                    })
                }, (err) => {
                    console.log(err)
                    reject(err);
                });
            })
        })
        // return new Promise((resolve, reject) => {
        //     firebase.auth().signInWithEmailAndPassword(email, password).then((res) => {
        //         firebase.firestore().collection("drivers").doc(res.uid).get().then(snapshot => {
        //             if(snapshot.exists){
        //                 let user: any = { id: snapshot.id, ...snapshot.data() } as UserModel;
        //                 user.type = "drivers";
        //                 this.storage.clear();
        //                 this.storage.set('currentUser', JSON.stringify(user));
        //                 this.user.next(user)
        //                 resolve(user)
        //             }else{
        //                 firebase.firestore().collection("stores").doc(res.uid).get().then(snap => {
        //                     if(snap.exists){
        //                         let user: any = { id: snap.id, ...snap.data() } as UserModel;
        //                         user.type = "stores";
        //                         this.storage.clear();
        //                         this.storage.set('currentUser', JSON.stringify(user));
        //                         this.user.next(user)
        //                         resolve(user)
        //                     }else{
        //                         reject("   !")
        //                     }
        //                 }, (err) => {
        //                     console.log(err)
        //                     reject(err);
        //                 });
        //             }
        //         }, (err) => {
        //             console.log(err)
        //             reject(err);
        //         });
        //     }, (err) => {
        //         console.log(err)
        //         reject("   !")
        //     })
        // })
    }
    getUser(): Promise<UserModel> {
        return new Promise((resolve, reject) => {
            firebase.firestore().collection("employees").doc(this._user.id).get().then((user) => {
                resolve({ uid: user.id, ...user.data() } as UserModel);
            }, error => reject(error));
        });
    }
    logout() {
        this.storage.clear();
        this.storage.remove('currentUser');
        firebase.auth().signOut();
    }
}


