import { Component, OnInit } from '@angular/core';
import { ViewController, ModalController, NavController, IonicPage } from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthProvider, IonicProvider } from '../../providers';
import { CustomValidators } from 'ng2-validation';
import { HomePage } from '..';
import firebase from 'firebase/app';
import 'firebase/firestore';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage implements OnInit {

  login_form: FormGroup;
  isLoading = false;
  constructor(
    private viewCtrl: ViewController,
    private modalCtrl: ModalController,
    private formBuilder: FormBuilder,
    private auth$: AuthProvider,
    private ionic$: IonicProvider,
    private navCtrl: NavController,
  ) { }


  ngOnInit() {
    this.login_form = this.formBuilder.group({
      email: ['', [Validators.required, CustomValidators.email]],
      password: ['', [Validators.required]]
    });
    this.signIn(this.login_form);
  }

  signIn({ value, valid }: { value: { email: string; password: string; }, valid: boolean }) {
    if (valid) {
      console.log('here')
      // @TODO:://  permenant delete loading .
      // this.isLoading = true
      this.auth$.logIn(value).then(
        (res) => {
          console.log('1 : ', res);
          this.isLoading = false
          this.auth$.start_listening();
          this.navCtrl.setRoot(HomePage)
        },
        (error) => {
          console.log('2 : ', error);
          if(error.code == "auth/wrong-password" || error.code == "auth/user-not-found"){
            this.ionic$.presentToast("      !")
          }else{
            this.ionic$.presentToast("   !")
          }
          this.isLoading = false
        });
    }
  }
}



