import { Injectable, Inject } from '@angular/core';
import { FirebaseApp } from 'angularfire2';
import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/storage';

@Injectable()
export class StorageProvider {

    storage: any;

    constructor(
        @Inject(FirebaseApp) firebaseApp: any
    ) {
        this.storage = firebase.storage();
    }

    b64toBlob(b64Data, contentType) {
        contentType = contentType || '';
        var sliceSize = 512;
        var byteCharacters = atob(b64Data);
        var byteArrays = [];
       
        for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
          var slice = byteCharacters.slice(offset, offset + sliceSize);
       
          var byteNumbers = new Array(slice.length);
          for (var i = 0; i < slice.length; i++) {
            byteNumbers[i] = slice.charCodeAt(i);
          }
       
          var byteArray = new Uint8Array(byteNumbers);
       
          byteArrays.push(byteArray);
        }
       
        var blob = new Blob(byteArrays, { type: contentType });
        return blob;
    }
    put(file, basePath="chat_imgs") : any {
        return new Promise((resolve, reject) => {
            var storageRef = firebase.storage().ref(basePath).child(this.random_file_name()+".jpg");
            storageRef.put(file, { contentType:'image/jpg' }).then(function(snapshot) {
                resolve(snapshot.downloadURL)
            }, error => reject(error));
        })
    }
    removeFile(picture){
        return new Promise((resolve, reject) => {
            picture = picture.split('https://firebasestorage.googleapis.com/v0/b/services-6e871.appspot.com/o/images%2F').pop().split("?alt=media").shift();
            var storageRef = firebase.storage().ref("images").child(picture);
            storageRef.delete();
        })
    }
    random_file_name(){
        return (Date.now().toString(36) + Math.random().toString(36).substr(2, 5)).toUpperCase();
    }
}


