import { Injectable } from '@angular/core';
import * as firebase from 'firebase/app';
import { AuthProvider } from './auth.provider';

@Injectable()
export class ChatProvider {
    
    constructor(private auth$: AuthProvider) {}
    send_message(data, store_id) {
        return new Promise((resolve, reject) => {
            firebase.firestore().collection("chat").add({
                user: firebase.firestore().collection("stores").doc(store_id),
                sender: firebase.firestore().collection("employees").doc(this.auth$._user.id),
                from: "admin",
                ...data,
                created_at: new Date().getTime()
            }).then((doc) => {
                resolve(doc)
            }, err => reject(err))
        })
    }
    listen_messages(store_id){
        return firebase.firestore().collection("chat").where("user", "==", firebase.firestore().collection("stores").doc(store_id)).orderBy('created_at');
    }
}




