import { Component, OnDestroy } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { OrderProvider, IonicProvider, AuthProvider } from '../../providers';
import firebase from 'firebase/app';
import 'firebase/firestore';
import { MessagingPage } from '../messaging/messaging';

@IonicPage()
@Component({
  selector: 'page-stores',
  templateUrl: 'stores.html',
})
export class StoresPage implements OnDestroy {
  stores = [];
  isLoading = true;
  no_data = false;
  end = null;
  all_stores = [];
  hide_iscroll = false;
  no_data_options = {
    text: "     !"
  }
  // search vars
  search_keyword;
  // status var
  status_select = "";
  constructor(public navCtrl: NavController, public navParams: NavParams, private auth$: AuthProvider,
    private order$:OrderProvider, private ionic$: IonicProvider) {
    this.get_stores();
  }
  get_stores(infiniteScroll = null) {
    if(!infiniteScroll) this.isLoading = true;
    this.no_data = false;

    let stores_ref:any = firebase.firestore().collection("stores").limit(20).orderBy('created_at', 'desc')
    if(this.end) stores_ref = stores_ref.startAfter(this.end);
    stores_ref.get().then(snapshot => {
        if(snapshot.empty) {
          if(!this.stores.length) this.no_data = true;
          this.hide_iscroll = true;
        }
        snapshot.docs.forEach((doc, index) => {
          let store: any = { id: doc.id, ...doc.data() }
          store.created_at = new Date(store.created_at)
          this.stores.push(store)
          firebase.firestore().collection("chat").where("user", "==", doc.ref).orderBy('created_at', 'desc').limit(1).get().then((messages_snap) => {
            if(!messages_snap.empty){
              let str_idx = this.stores.findIndex((st) => st.id == store.id);
              this.stores[str_idx].latest_message = { id: messages_snap.docs[0].id, ...messages_snap.docs[0].data() };
            }
          })
          if(index == snapshot.docs.length-1) {
            this.stores.sort((a, b) => {
              return b.created_at - a.created_at
            })
            this.end = this.stores[this.stores.length-1].created_at.getTime();
            if(infiniteScroll){infiniteScroll.complete();}else{this.isLoading = false;}
          }
        })
    }, err => {
      if(infiniteScroll){infiniteScroll.complete();}else{this.isLoading = false;}
      if(!this.stores.length) this.no_data = true;
      console.log(err)
    })
  }
  search(){
    if(this.search_keyword){
      this.isLoading = true;
      firebase.firestore().collection("stores").limit(8).orderBy("name").startAt(this.search_keyword).endAt(this.search_keyword+'\uf8ff').get().then(snapshot => {
        if(snapshot.empty){
          this.ionic$.presentToast("     ");
          this.no_data = true;
          this.isLoading = false;
        }else{
          this.stores = [];
          snapshot.docs.forEach((doc, index) => {
            if(doc.exists){
              let store: any = { id: doc.id, ...doc.data() }
              store.created_at = new Date(store.created_at)
              if(!this.stores.find((or) => or.id == store.id)) this.stores.push(store)
              if(this.stores.length == snapshot.docs.length) this.isLoading = false
            }else{
              if(this.stores.length == snapshot.docs.length) this.isLoading = false
            }
          })
        }
      });
    }else{
      this.stores = [];
      this.get_stores();
    }
  }
  ngOnDestroy(){}
  openStore(index){
    this.navCtrl.push(MessagingPage, { store: this.stores[index] })
  }
}



